<template>
  <div class="modal fade"
       ref="myPageModal"
       tabindex="-1"
       role="dialog"
       aria-hidden="true">
    <div class="modal-dialog"
         style="max-width:1140px;">
      <div class="modal-content"
           style="height: 500px;">
        <div class="modal-header">
          <button type="button"
                  class="close"
                  data-dismiss="modal"
                  @click="toggle"
                  aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="text-center">
            <img class="rounded-circle"
                 style="height:2.5rem;"
                 src="./../../static/img/myIcon.png">
            <div>{{me.name}}</div>
            <div>
              <span class="badge badge-secondary mr-1"
                    v-for="(badge, index) in me.tags"
                    :key="index">{{badge}}</span>
            </div>
            <div>
              <span class="font-weight-light form-control-plaintext"
                    style="font-size:0.8rem;">
                {{me.label}}
              </span>
            </div>
            <div>
              <span v-for="(page,index) in me.pages"
                   :key="index">
                <i :class="page.icon.class"
                   v-if="page.icon.type==='font face'"
                   :title="page.link"
                   @click="jump(page.link)"></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      me: {}
    }
  },
  created () {
    this.reload()
  },
  methods: {
    reload () {
      this.$http.get('/service/user/1').then(res => {
        return res.json()
      }).then(data => {
        this.me = data
      })
    },
    toggle () {
      $(this.$refs.myPageModal).modal('toggle')
    },
    jump (pathParam) {
      window.open(pathParam, '_blank')
    }
  }
}
</script>
<style>
</style>



