<template>
  <div class="modal fade"
       ref="searchModal"
       tabindex="-1"
       role="dialog"
       aria-hidden="true">
    <div class="modal-dialog"
         style="max-width:1140px;">
      <div class="modal-content"
           style="height: 500px;">
        <div class="modal-header">
          <button type="button"
                  class="close"
                  data-dismiss="modal"
                  @click="toggle"
                  aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <form>
            <div class="input-group w-75 m-auto">
              <input type="text"
                     class="form-control"
                     v-model="searchString"
                     @keyup="$event.keyCode==13?search:false"
                     :placeholder="$t('searchArticlePlaceHolder')">
              <div class="input-group-append">
                <button type="button"
                        :disabled="searchString?disabled:false"
                        class="btn btn-primary"
                        @click="search">{{$t("search")}}</button>
              </div>
            </div>
          </form>
          <div class="list-group list-group-flush m-4 pre-scrollable">
            <div class="list-group-item list-group-item-action"
                 v-for="(item, index) in searchResult"
                 :key="index">
              <router-link :to="'/article/'+item.id"
                           target="_blank">{{item.name}}</router-link>
              <div>
                <span class=" align-baseline">{{item.createTime}}</span>
                <span v-for="(tag, index) in item.tags"
                      :key="index"
                      class="badge badge-info m-1">{{tag.text}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      articles: [],
      searchString: '',
      searchResult: []
    }
  },
  created () {
    this.reload()
  },
  methods: {
    reload () {
      // this.$http.get('/service/article').then(res => { return res.json() }).then(data => { this.articles = data })
    },
    toggle () {
      $(this.$refs.searchModal).modal('toggle')
    },
    search () {
      this.$http.put('/service/article/search', this.searchString).then(res => {
        return res.json()
      }).then(data => {
        this.searchResult = data
      })
    }
  }
}
</script>
<style>
</style>



